var num=0;
export default{
	data:function(){
		return {
			title:"我是标题",
			content:"我是内容",
			nickname:"这是昵称",
			list:[],
			cls:"cl-red",
			style:"font-size:36px;color:f30;",
			num:2
		}
	},
	onLoad:function(ops){
		this.getPage();
	},
	onReachBottom:function(){
		this.getList();
	},
	onPullDownRefresh:function(){
		this.getPage();
		uni.stopPullDownRefresh();
	},	
	methods:{
		clickMe:function(msg){
			 
			if(typeof(msg)=="string"){
				alert(msg)
			}else{
				
				alert("我被击中了");
			}
			
		},
		getPage:function(){
			console.log("加载了");
			var list=[];
			for(var i=0;i<50;i++){
				num++;
				list.push('这是第'+num+'条新闻');
			}
			this.list=list;
			uni.setNavigationBarTitle({
				title:"这是老雷uniApp的hello world"
			})
		},
		getList:function(){
			var list=this.list;
			for(var i=0;i<50;i++){
				num++;
				list.push('这是第'+num+'条新闻');
			}
			this.list=list;
		}
	}
}
